import closeImg from '@/images/close@2x.png'

const creatVideo = (src, option) => {

  const box = creatBox()
  const mask = creatMask()
  const closeBtn = creatCloseBtn()
  const video = creatVideoDom(src, option)
  box.appendChild(mask)
  box.appendChild(video)
  box.appendChild(closeBtn)
  closeBtn.addEventListener('click', () => {
    box.parentElement.removeChild(box)
  })
  // const handleEsc = (event) => {
  //   var code = (event.keyCode ? event.keyCode : event.which); 
  //   if (code == 27){
  //     box.parentElement.removeChild(box)
  //     window.removeEventListener('keypress', handleEsc)
  //   }
  // }
  
  // window.addEventListener('keypress', handleEsc)
  return box
}

function creatBox() {
  const box = document.createElement('div')
  box.style.position = 'flex'
  box.style.top = '0'
  box.style.right = '0'
  box.style.bottom = '0'
  box.style.left = '0'
  box.style.zIndex = 9998
  return box
}
function creatMask() {
  const box = document.createElement('div')
  box.style.position = 'absolute'
  box.style.top = '0'
  box.style.right = '0'
  box.style.bottom = '0'
  box.style.left = '0'
  box.style.zIndex = 10
  box.style.backgroundColor = "rgba(0, 0, 0, 0.6)"
  return box
}
function creatCloseBtn() {
  const closeBtn = document.createElement('img')
  closeBtn.src = closeImg
  closeBtn.style.position = 'absolute'
  closeBtn.style.top = '2vw'
  closeBtn.style.right = '2vw'
  closeBtn.style.zIndex = 10
  closeBtn.style.width = "24px"
  closeBtn.style.height = "24px"
  closeBtn.style.cursor = "pointer"
  closeBtn.style.transition = "transform 0.3s"
  closeBtn.style.transform = "rotateZ(0deg)"
  closeBtn.addEventListener('mouseenter', () => {
    closeBtn.style.transform = "rotateZ(-90deg)"
  })
  closeBtn.addEventListener('mouseleave', () => {
    closeBtn.style.transform = "rotateZ(0deg)"
  })
  return closeBtn
}
function creatVideoDom(src, option) {
  const video = document.createElement('video')
  video.style.position = 'absolute'
  video.style.top = '50%'
  video.style.left = '50%'
  video.style.transform = 'translate(-50%, -50%'
  video.style.zIndex = 20
  video.style.width = "90%"
  video.style.height = "auto"
  video.src=src
  video.controls= 'controls'
  video.autoplay = option || {}
  video.addEventListener('click', (event) => {
    event.stopPropagation()
  })
  video.addEventListener('load', () => {
    video.play()
  })
  return video
}

export default creatVideo;
