<template>
  <div class="bet-shortcut flex jc-center">
    <div v-if="shortcutEpoch&&roundInfo" class="flow box">
      <div class="top-arrow ta-center text" :class="roundInfo.betType==='0'?'up':'down'">
        {{ language.bet_direction }}
        <p class="top-text flex jc-center ai-center">
          <span v-if="roundInfo.betType === '0'">{{ language.up }} - {{ roundInfo.bullRate.toRate() }}x</span>
          <span v-else>{{ language.down }} - {{ roundInfo.bearRate.toRate() }}x</span>
        </p>
      </div>
      <div class="no ta-center"># {{ shortcutEpoch || '——' }}</div>
      <div class="bg-radius flex jc-between ai-center">
        <div class="flex ai-center">
          <img class="coin icon-s" :src="coinMap[payCoin]" alt="">
          <p class="text">{{ language.bet_num }}<br>{{ roundInfo.betAmount.myFixed() || '——' }}</p>
        </div>
        <span class="suffix text">{{ payCoin }}</span>
      </div>
      <div class="bg-radius flex jc-between ai-center">
        <div>
          {{ language.tip_current_round }}
          <p class="clock flex ai-center text">
            <img class="icon" src="../../images/option/time.png" alt=""> {{ language.resolution_5 }}
          </p>
        </div>
        <div class="ta-right">{{ language.pair }}<p class="text">BNB/USDT</p></div>
      </div>
      <div class="arrow flex jc-center">
        <img class="icon-xs" src="../../images/option/zs-arrow.png" alt="">
      </div>
      <div class="bg-radius flex jc-between ai-center">
        <div v-if="flowStep>1">{{ language.end_price }}<p class="text">$ {{ roundInfo.closePrice.myFixed() }}</p></div>
        <div v-else>{{ language.latest_price }}<p class="text">$ {{ currentPrice[symbol]?.myFixed() || '——' }}</p>
        </div>
        <div class="ta-right">{{ language.lock_price }}<p class="text">$
          {{ flowStep > 0 ? roundInfo.lockPrice.myFixed() : '——' }}</p></div>
      </div>
      <div class="delta flex jc-center ai-center fs-16">
        <span v-if="!delta">——</span>
        <template v-else-if="delta.gt(0)">
          <span class="green">$ +{{ delta.myFixed() }}</span>
          <img class="icon" src="../../images/option/up.png" alt="">
        </template>
        <template v-else-if="delta.lt(0)">
          <span class="red">$ {{ delta.myFixed() }}</span>
          <img class="icon" src="../../images/option/down.png" alt="">
        </template>
        <span v-else class="text">$ 0</span>
      </div>
      <div class="w100p ta-center">
        <p class="b-tip flex jc-between">
          <span v-if="flowStep===0">{{ language.lock_clock }}</span>
          <span v-else-if="flowStep===1">{{ language.end_clock }}</span>
          <span v-else>
            {{ language.round_ended }}：
            <span v-if="isWinner" class="green">{{ language.win }}</span>
            <span v-else class="red">{{ language.lose }}</span>
          </span>
          <span>{{ language.btn_betted }}</span>
        </p>
        <p>
          <m-button v-if="flowStep<2" class="bet-btn border-gray hdr-el-gray" disabled>
            <b class="text fs-18">{{ countDisplay }}</b>
          </m-button>
          <m-button v-else-if="isWinner" class="bet-btn border-green hdr-el-green green" :loading="claimLoading"
                    @click="claim">
            <img class="icon-s" src="../../images/option/jiangbei.png" alt=""> {{ language.btn_claim }}
          </m-button>
          <m-button v-else class="bet-btn border-red hdr-el-red red" :loading="claimLoading" @click="claim">
            <img class="icon-s" src="../../images/option/nft-copy.png" alt=""> {{ language.btn_claim }}
          </m-button>
        </p>
      </div>
      <div v-if="flowStep<2&&isCalculating" class="mask flex jc-center ai-center">
        <div class="ta-center">
          <i class="icon-loading"></i><br>
          {{ flowStep === 0 ? language.tip_locking : language.tip_clearing }}...
        </div>
      </div>
    </div>
    <div v-else class="bet box">
      <div class="top-arrow ta-center">
        {{ language.pool }}
        <p class="text flex jc-center ai-center">
          <img class="icon-xxs" :src="coinMap[payCoin]" alt="">
          <b class="top-text fs-16">{{ currentRound.totalAmount?.myFixed() || 0 }}</b>
        </p>
      </div>
      <div class="no ta-center"># {{ currentEpoch || '——' }}</div>
      <div class="bg-radius flex jc-between ai-center">
        <label class="input-row flex1 flex">
          <input class="flex1" type="number" v-model="amountDisplay" @input="amountInput"
                 :placeholder="language.tip_2_3">
        </label>
        <span class="suffix text">{{ payCoin }}</span>
      </div>
      <div class="bg-radius flex jc-between ai-center">
        <div>
          {{ language.tip_current_round }}
          <p class="clock flex ai-center text">
            <img class="icon" src="../../images/option/time.png" alt=""> {{ language.resolution_5 }}
          </p>
        </div>
        <div class="ta-right">{{ language.pair }}<p class="text">BNB/USDT</p></div>
      </div>
      <div class="arrow flex jc-center">
        <img class="icon-xs" src="../../images/option/zs-arrow.png" alt="">
      </div>
      <div class="bg-radius flex jc-between ai-center">
        <div>{{ language.latest_price }}<p class="text">$ {{ currentPrice[symbol]?.myFixed() || '——' }}</p></div>
        <div class="ta-right">{{ language.lock_clock }}<p class="text fs-18"><b>{{ countDisplay }}</b></p></div>
      </div>
      <div class="arrow flex jc-center">
        <img class="icon-xs" src="../../images/option/zs-arrow.png" alt="">
      </div>
      <div class="flex jc-between">
        <div class="w50p ta-center">
          <p class="b-tip text">{{ currentRound.bullRate?.toRate() || 2 }}x {{ language.profit }}</p>
          <p>
            <m-button v-if="!isAuthorized" class="bet-btn hdr-el-blue" @click="doApprove" :loading="isBetting">
              <span class="text">{{ language.btn_authorize }}</span>
            </m-button>
            <m-button v-else-if="betDisabled" class="bet-btn border-gray hdr-el-gray" disabled>
              <img class="icon-xs" src="../../images/option/disable1.png" alt=""/>
              <span class="gray">{{ language.btn_betted }}</span>
            </m-button>
            <m-button v-else class="bet-btn border-green hdr-el-green" :loading="isBetting"
                      @click="bet('betBull')" :disabled="buyAmount.isZero()"
            >
              <img class="icon-xs" src="../../images/option/look-up.png" alt=""/>
              <span class="green">{{ language.btn_up }}</span>
            </m-button>
          </p>
        </div>
        <div class="w50p ta-center">
          <p class="b-tip text">{{ currentRound.bearRate?.toRate() || 2 }}x {{ language.profit }}</p>
          <p>
            <m-button v-if="!isAuthorized" class="bet-btn hdr-el-blue" @click="doApprove" :loading="isBetting">
              <span class="text">{{ language.btn_authorize }}</span>
            </m-button>
            <m-button v-else-if="betDisabled" class="bet-btn border-gray hdr-el-gray" disabled>
              <img class="icon-xs" src="../../images/option/disable1.png" alt=""/>
              <span class="gray">{{ language.btn_betted }}</span>
            </m-button>
            <m-button v-else class="bet-btn border-red hdr-el-red" :loading="isBetting"
                      @click="bet('betBear')" :disabled="buyAmount.isZero()"
            >
              <img class="icon-xs" src="../../images/option/look-down.png" alt=""/>
              <span class="red">{{ language.btn_down }}</span>
            </m-button>
          </p>
        </div>
      </div>
      <div v-if="isCalculating" class="mask flex jc-center ai-center">
        <div class="ta-center"><i class="icon-loading"></i><br>{{ language.tip_locking }}...</div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapActions, mapGetters, mapState} from "vuex";
import {inputNumberFixer, optionRoundConvertor} from "@/common/dataConvertor";
import {BN} from "@/common/util";
import MButton from "@/components/mButton";
import {coinMap} from "@/common/walletConfig";

export default {
  name: "betShortcut",
  components: {MButton},
  data() {
    return {
      coinMap,
      isAuthorized:false,
      payCoin:'USDT',
      amountDisplay: '0.01',
      isBetting: false,
      symbol: 'BNBUSDT',
      resolution: '5',
      roundInfo: null,
      claimLoading: false,
      betReadyEpoch: sessionStorage.getItem('betReadyEpoch') || '',
    }
  },
  watch: {
    address(){
      this.getAllowance();
    },
    'currentRound.betAmount'(amount) {
      if (amount > 0 && this.betReadyEpoch === this.currentEpoch) {
        this.setShortcutEpoch(this.currentEpoch);
        this.getRoundInfo(this.currentEpoch);
      }
    },
    currentEpoch() {
      this.setBetReadyEpoch('');
      this.flowStep <= 2 && this.getRoundInfo(this.shortcutEpoch);
    }
  },
  computed: {
    ...mapGetters(['language']),
    ...mapState('option', ['currentRounds', 'contractName', 'currentPrice', 'shortcutEpoch']),
    ...mapState('app', ['address','balance']),
    currentRound() {
      return this.currentRounds[this.resolution]
    },
    currentEpoch() {
      return this.currentRound.epoch;
    },
    delta() {
      let {flowStep, roundInfo, symbol, currentPrice} = this;
      let {closePrice, lockPrice} = roundInfo;
      return flowStep > 1 ? closePrice.minus(lockPrice) : (flowStep === 1 && currentPrice[symbol]) ? currentPrice[symbol].minus(lockPrice) : '';
    },
    isWinner() {
      let delta = this.delta;
      let {betType, betAmount} = this.roundInfo;
      return delta && betAmount.gt(0) && ((delta.gt(0) && betType === '0') || (delta.lt(0) && betType === '1'))
    },
    flowStep() {
      return this.currentEpoch - this.shortcutEpoch;
    },
    isCalculating() {
      return this.currentRound.isCalculating;
    },
    betDisabled() {
      return this.currentRound.betAmount !== '0';
    },
    countDisplay() {
      return this.currentRound.countDisplay;
    },
    maxAmount(){
      return this.balance.USDT.toDisplay('balance');
    },
    buyAmount() {
      let {amountDisplay} = this;
      return amountDisplay ? new BN(amountDisplay).toWei('balance') : new BN(0);
    },
  },
  methods: {
    ...mapActions('option', ['setShortcutEpoch']),
    getAllowance() {
      let {address, resolution, contractName} = this;
      if (!address) return;
      this.$api.getOptionAllowance(address, resolution, contractName).then(num => this.isAuthorized = num !== '0')
    },
    doApprove(){
      let {address, resolution, contractName} = this;
      if (!address) return;
      this.isBetting = true;
      this.$api.approveOption(resolution, contractName).then(()=>{
        this.isBetting = false;
        this.isAuthorized = true;
      }).catch(()=>this.isBetting = false);
    },
    amountInput(e) {
      let val = e.target.value;
      let max = this.maxAmount;
      val = Number(val) > max ? max.toString(10) : val;
      this.amountDisplay = inputNumberFixer(val);
    },
    getRoundInfo(epoch) {
      let {resolution, address, contractName} = this;
      if (!address || !epoch) return;
      let p1 = this.$api.getRoundInfo(epoch, resolution, contractName);
      let p2 = this.$api.getBetInfoInRound(epoch, address, contractName);
      Promise.all([p1, p2]).then(([roundInfo, {amount, position}]) => {
        roundInfo = optionRoundConvertor(roundInfo);
        roundInfo.betAmount = new BN(amount).toDisplay('balance');
        roundInfo.betType = position;
        this.roundInfo = roundInfo;
      });
    },
    bet(betType) {
      let from = this.address, amount = this.buyAmount.toString(10);
      if (!from) this.$api.login();
      else {
        this.isBetting = true;
        this.setBetReadyEpoch(this.currentEpoch);
        this.$api.bet(betType, from, amount, this.resolution, this.contractName).then(() => {
          this.isBetting = false;
        }).catch(() => {
          this.isBetting = false;
        });
      }
    },
    claim() {
      this.claimLoading = true;
      this.$api.claimReward(this.shortcutEpoch, this.resolution, this.contractName).then(() => {
        this.claimLoading = false;
        this.setShortcutEpoch('');
        this.roundInfo = null;
      }).catch(() => {
        this.claimLoading = false;
      })
    },
    setBetReadyEpoch(epoch){
      if (epoch){
        this.betReadyEpoch = epoch;
        sessionStorage.setItem('betReadyEpoch', this.currentEpoch);
      }else{
        this.betReadyEpoch = '';
        sessionStorage.removeItem('betReadyEpoch');
      }
    }
  },
  mounted() {
    this.getAllowance();
    this.getRoundInfo(this.shortcutEpoch);
  }
}
</script>

<style lang="less" scoped>
.bet-shortcut {
  padding-top: 65px;
}

.box {
  width: 350px;
  height: 463px;
  padding: 0 20px;
  background-color: #191F2D;
  border-radius: 10px;
  position: relative;

  .top-arrow {
    width: 220px;
    height: 65px;
    line-height: 20px;
    padding-top: 14px;
    transform: translate(-50%, -100%);
    background: url("../../images/option/s-gray@2x.png") center no-repeat;
    background-size: 100% 100%;
    position: absolute;
    top: 0;
    left: 50%;

    &.up {
      background: url("../../images/option/s-green@2x.png") center no-repeat;
    }

    &.down {
      background: url("../../images/option/s-red@2x.png") center no-repeat;
    }

    img {
      margin-right: 10px;
    }
  }

  .top-text {
    line-height: 30px;
  }

  .no {
    line-height: 40px;
  }

  .input-row {
    height: 40px;
    padding-left: 15px;
    background-color: #191f2d;
    border-radius: 10px;
  }

  .coin {
    margin-right: 15px;
  }

  .suffix {
    margin-left: 20px;
  }

  .bg-radius {
    height: 60px;
    line-height: 26px;
    margin-bottom: 20px;
    padding: 0 20px;
    background-color: #353B4D;
    border-radius: 10px;

    .clock {
      height: 26px;

      img {
        margin-left: -7px;
      }
    }
  }

  .w100p {
    width: 100%;
  }

  .w50p {
    width: calc(50% - 10px);
  }

  .arrow {
    margin-bottom: 20px;
  }

  .delta {
    height: 25px;
    margin-bottom: 20px;
  }

  .b-tip {
    line-height: 20px;
    margin-bottom: 10px;
  }

  .bet-btn {
    width: 100%;
  }
}

.mask {
  border-radius: inherit;
}

.bet {
  margin-left: 20px;
}

@media screen and (max-width: 1024px) {
  .box {
    height: auto;
    padding-bottom: 15px;

    .bg-radius {
      margin-bottom: 10px;
    }

    .arrow {
      margin-bottom: 10px;
    }
  }
}

@media screen and (max-width: 540px) {
  .bet-shortcut {
    width: 100%;
  }

  .box {
    width: 100%;
    max-width: 450px;
    margin-left: 0;
    padding: 0 15px 15px;

    .bg-radius {
      padding: 0 15px;
    }
  }
}
</style>