<template>
  <div class="home page">
    <div class="part-1">
      <div class="inner w1200 flex jc-around ai-center">
        <div class="desc">
          <h1 class="text fs-35">{{ language.title_1 }}</h1>
          <p>{{ language.p_1_1 }}</p>
          <p>{{ language.p_1_2 }}</p>
          <p>{{ language.p_1_3 }}</p>
        </div>
        <bet-shortcut class="shortcut"/>
      </div>
    </div>
    <div class="part-2">
      <ul class="list-1 w1200 flex jc-between">
        <li class="item">
          <img src="../images/home/Blockchain.png" alt=""/>
          <h2>{{ language.holder_tt }}</h2>
          <p>{{ language.holder_p_1 }} <br>{{ language.holder_p_2 }}</p>
        </li>
        <li class="item">
          <img src="../images/home/p1.png" alt=""/>
          <h2>{{ language.option_tt }}</h2>
          <p>{{ language.option_p_1 }} <br>{{ language.option_p_2 }}</p>
        </li>
        <li class="item">
          <img src="../images/home/lottery.png" alt=""/>
          <h2>{{ language.open_tt }}</h2>
          <p>{{ language.open_p_1 }} <br>{{ language.open_p_2 }}</p>
        </li>
      </ul>
      <h1 class="text fs-35">{{ language.title_2 }}</h1>
      <div class="list-2">
        <ul class="w1200 flex jc-center">
          <li class="item" v-for="(step,i) in steps"
              @click="stepIndex=i"
              :key="i">
            <div class="circle" :class="{active:stepIndex===i}">
              {{ language[step.text1] }}<br>{{ language[step.text2] }}
            </div>
          </li>
        </ul>
      </div>
      <div class="card w1200">
        <bet-step :active-index="stepIndex"/>
      </div>
      <div class="list-2">
        <ul class="w1200 flex jc-center">
          <li class="item" v-for="(step,i) in steps1"
              @click="step1Index=i"
              :key="i">
            <div class="circle" :class="{active:step1Index===i}">
              {{ language[step.text1] }}
            </div>
          </li>
        </ul>
      </div>
      <div class="card w1200">
        <video
          v-for="(item, index) in 3"
          :key="item"
          v-show="step1Index === index"
          class="home-video"
          :src="steps1[index].link"
          autoplay
          playsinline
          muted
          @click="handleOpenVideo(steps1[index].link)"
        />
      </div>
      <div class="safe-info w1200">
        <h1 class="text fs-35">{{ language.title_3 }}</h1>
        <p>{{ language.p_2_1 }} <br>{{ language.p_2_2 }}</p>
        <img src="../images/home/computer.png" alt=""/>
      </div>
    </div>
  </div>
</template>

<script>
import BetStep from "@/views/home/betStep";
import {mapGetters, mapState} from "vuex";
import BetShortcut from "@/views/home/betShortcut";
import project1 from '@/video/project1.mp4'
import project2 from '@/video/project2.mp4'
import project3 from '@/video/project3.mp4'
import creatVideo from './creatVideo'

export default {
  name: 'home',
  components: {BetShortcut, BetStep},
  computed: {
    ...mapState('option',['symbol','resolution']),
    ...mapGetters(['language']),
  },
  data() {
    return {
      project1,
      project2,
      project3,
      steps: [
        {text1: 'text_1_1',text2:'text_1_2'},
        {text1: 'text_2_1',text2:'text_2_2'},
        {text1: 'text_3_1',text2:'text_3_2'},
        {text1: 'text_4_1',text2:'text_4_2'},
        {text1: 'text_5_1',text2:'text_5_2'},
      ],
      steps1: [
        {text1: 'text_6_1', link: project1},
        {text1: 'text_6_2', link: project2},
        {text1: 'text_6_3', link: project3},
      ],
      stepIndex: 0,
      step1Index: 0,
    };
  },
  methods:{
    toTrade() {
      this.$router.push({path: '/option', query: {symbol: this.symbol, resolution: this.resolution}})
    },
    handleOpenVideo(link) {
      const dom = creatVideo(link)
      document.body.appendChild(dom)
    },
  }
};
</script>

