}
.part-1 {
  background-color: #222a3d;
  .inner {
    height: 700px;
    padding: 0 30px;
  }
  .desc {
    margin-right: 50px;
    h1{
      margin-bottom: 40px;
    }
    p {
      margin-top: 10px;
      opacity: 0.7;
    }
  }
  .shortcut{
    margin-top: 65px;
  }
}
.part-2 {
  text-align: center;
}
.list-1 {
  padding: 100px 0;
  li {
    width: 370px;
  }
  img {
    height: 220px;
  }
  h2 {
    font-size: 25px;
    color: #FFFFFF;
    font-weight: bold;
    margin-bottom: 25px;
  }
  p {
    line-height: 2;
  }
}
.list-2 {
  padding: 80px 0;
  background: url("../images/home/Shape2copy2@2x.png") center center repeat-x;
  li {
