    height: 300px;
    position: relative;
    &:nth-child(odd) {
      .circle {
        top: 0;
      }
    }
    &:nth-child(even) {
      .circle {
        bottom: 0;
      }
    }
  }
  .circle {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 176px;
    height: 176px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFFFFF;
    font-size: 14px;
    letter-spacing: 3px;
    line-height: 1.2;
    background: url("../images/home/switch2.png") center;
    background-size: cover;
    cursor: pointer;
    border-radius: 50%;
    &:hover, &.active {
      color: #171723;
      background: url("../images/home/switch.png") center;
      background-size: cover;
    }
  }
}
.card {
  background-color: #353B4D;
  border-radius: 20px;
  color: #FFFFFF;
}
.safe-info {
  padding: 150px 0 0;
  p {
    margin: 50px 0 20px;
    line-height: 2;
    opacity: 0.6;
  }
}
footer {
  line-height: 53px;
  background-color: #191f2d;
  .copy {
    margin-left: 80px;
  }
}
@media screen and (max-width: 1024px) {
  .page{
    padding: 0;
  }
  .part-1{
    .inner{
      min-width: auto;
      height: auto;
      padding: 40px 30px;
    }
    .desc{
      margin-right: 0;
      h1{
        margin-bottom: 20px;
      }
    }
  }
  .list-1 {
    padding: 80px 0;
  }
  .safe-info {
